.About{
    min-height: 100vh;
    position: relative;
    top: 10vh;
    margin: 10vw;
    display: flex;
    flex-wrap: wrap-reverse;
    justify-content: space-between;

}
.About> div {
    align-self: center;
    margin: auto;

}
.Image{
    border: 5px solid red;
    height: 300px;
    width: 290px;
}
.Image img{
    margin: 10px;

}
.description h1{
    font-size: 6vw;
    text-align: center;
}
.description h2{
    text-align: center;
}
.description{

    font-family: 'Montserrat', sans-serif;
    width: 50vw;
    min-width: 400px;

}
